import React from "react";
import ReactDom from "react-dom";
import "./index.css";
class Board extends React.Component{
  constructor(props){
    super(props);
    this.state={
      name:["ROCK","PAPER","SCISSOR"],
      win:0,
      winner:"",
      clas:"",
      l:4,
      Name:"element",
    }
  } 
  click(i){
    return(
      <div className="element">
          <button onClick={(event)=>{
            const valu=Math.floor((Math.random()*3));
            if(i===valu){
              this.setState({winner:"DRAW",clas:"white"});
            }
            else if((i===0&&valu===2)||(i===1&&valu===0)||(i===2&&valu===1)||(i===2&&valu===1)){
              this.setState({winner:"WINNER",win:this.state.win+1,clas:"green"});
            } 
            else{
              this.setState({winner:"LOSE",win:this.state.win-1,clas:"red"});
              this.setState({win:this.state.win-1});
            }
          }}>{this.state.name[i]}</button>
      </div>
    )
  }
  render(){
    return(
      <div className="playarea">
        {this.click(0)}
        {this.click(1)}
        {this.click(2)}
        <div className="winner">
            <h3>Score: {this.state.win}</h3>
            <p className={this.state.clas}>{this.state.winner}</p>
        </div>
      </div>
    )
  }
}
class Game extends React.Component{
  render(){
    return(
      <div className="main">
        <h1 id="heading">ROCK PAPER SCISSOR</h1>
        <Board/>
      </div>
    );
  }
}
ReactDom.render(<Game/>,document.getElementById("root"));